.mfp-white-block
    background-color: $white
    box-shadow: 0 0 60px 0 rgba(45, 49, 67, 0.2)
    max-width: 470px
    width: 100%
    position: relative
    padding: 20px
    margin-left: auto
    margin-right: auto
    @media (max-width: $md4+px)
        padding: 30px
    @media (max-width: $md3+px)
        padding: 40px
    @media (max-width: $md2+px)
        padding: 50px
    @media (max-width: $md1+px)
        padding: 60px
